
///// Components /////
h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  color: #fff;
}

.btn:not([class^="btn-outline-"]) {
  //border-color: transparent;
  //@include hover-focus() { border-color: transparent; }
}

.breadcrumb-style1 .breadcrumb-item a,
.breadcrumb-style2 .breadcrumb-item a,
.breadcrumb-style3 .breadcrumb-item a {
  color: $gray-600;
}

.breadcrumb-style1 .breadcrumb-item {
  + .breadcrumb-item::before {
    color: $gray-700;
    font-weight: 400;
  }
}

.card {
  border-width: 0;
  background-color: $gray-900;
  color: $gray-500;
}

.list-label {
  background-color: darken($gray-900, 2%);
  border-color: lighten($gray-900, 5%);
}

.list-item { border-color: lighten($gray-900, 5%); }

.list-group-item {
  background-color: inherit;
  border-color: rgba(#fff, .025);
}

.dropdown-divider { border-color: rgba(#fff, .06); }

.link-01 {
  color: rgba(#fff, .85);
  @include hover-focus() { color: #fff; }
}

.link-02 {
  color: $gray-300;
  @include hover-focus() { color: #fff; }
}

.link-03 { @include hover-focus() { color: #fff; } }

.progress { background-color: $gray-700; }

.tx-color-01 { color: #fff; }
.tx-color-02 { color: $gray-300; }
.tx-color-04 { color: rgba($gray-700, .7); }

.table { color: $gray-300; }

.divider-text {
  &::before,
  &::after { background-color: lighten($gray-900, 5%); }
}

.form-control {
  background-color: transparent;
  border-color: $gray-800;
  color: #fff;

  &::placeholder { color: $gray-700; }

  &:focus { background-color: transparent; color: #fff; }
}

.nav-classic .nav-link {
  color: $gray-500;
}

.nav-link {
  &.disabled { color: $gray-700 !important; }
}

.img-group .img {
  background-color: $gray-900;
}

.search-form button {
  border-color: $gray-800;
}

.modal-content {
  background-color: $gray-900;
  color: #fff;
}

.custom-select {
  background: transparent url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' width='4' height='5' viewBox='0 0 4 5'><path fill='white' d='M2 0L0 2h4zm0 5L0 3h4z'/></svg>") right 0.625rem center/8px 10px no-repeat;
  border-color: $gray-800;

  &:not(:empty) {
    color: white;
  }
}

.bd-bottom {
    border-bottom: 1px solid $gray-800 !important;
}

.form-control:disabled, .form-control[readonly] {
    background-color: darken($gray-900, 10%);
}

.df-example {
  background-color: darken($gray-900, 15%);
  border-color: $gray-800;

  &:before {
    color: #fff;
  }
}

.custom-file-label {
  border-color: $gray-800;
  background-color: transparent;

  &:after {
    background-color: $gray-800;
    color: #fff;
  }
}

.bg-dark {
    color: #fff !important;

    a {
      color: #fff;
    }
}

.chosen-container-multi .chosen-choices,
.chosen-container-single .chosen-single {
  border-color: $gray-800;
}

.chosen-container-multi .chosen-choices li.search-field input[type="text"],
.chosen-container-single .chosen-single span {
  color: #fff !important;
}

.table-dark {
  background-color: transparent;

  thead {
    background-color: darken($gray-800, 10%);
    th {
      color: #fff !important;
    }
  }

  tbody {
    color: #fff !important;

    td {
      border-color: transparent !important;
    }
  }
}

div.dt-button-collection {
  background-color: $gray-800 !important;

  .dt-button {
    a {
      color: #fff !important;
    }

    &:not(.active) {
      background-color: $gray-200 !important;

      a {
        color: $gray-900 !important;
      }
    }
  }
}

div.dt-button-info {
  background-color: $gray-800 !important;
  color: #fff !important;

  h2 {
    background-color: $gray-700 !important;
    border-color: $gray-600 !important;
  }
}

.custom-file-label-alt {
  @include hover-focus { color: lighten($color-brand-01, 5%); }
}

.signin-hero {
  background: darken($gray-800, 10%) url(../img/admin-signin-dark.svg) center center no-repeat;
  background-size: 60%;
}